.Compare {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Compare__Filter {
  width: 97%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 1rem;
  margin-bottom: 1rem;
}
.Compare__Filter__Date {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Compare__Filter__Button:hover {
  cursor: pointer;
}
.Compare__Filter__Date__Input {
  padding: 0.2rem 0.5rem;
}
.Compare__Chart {
  width: 97%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 1rem;
  margin-bottom: 1rem;
}
.CompareOverall {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.CompareOverall__Pie {
  display: flex;
  flex-direction: row;
}
/*-------------- Responsive Design ------------------- */
/*-------------- MOBILE ------------------- */
@media screen and (max-width: 450px) {
  .Compare__Filter {
    width: 97%;
    height: 20vh;
    /* display: flex; */
    flex-direction: column;
  }
  .CompareOverall__Pie {
    flex-direction: column;
  }
}

/*-------------- TABLET ------------------- */
@media screen and (max-width: 850px) and (min-width: 450px) {
  .CompareOverall__Pie {
    flex-direction: column;
  }
}
